<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <material-card
          color="primary"
          title="Pesquisar Lotes de RPS">
          <v-card class="elevation-0">
            <v-card-text>
              <v-form>
                <v-layout
                  row
                  wrap>
                  <v-flex md12>
                    <core-filtro-rede-empresa-loja
                      v-model="filtros"
                      hidden-loja
                      show-todas
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-autocomplete
                      v-model="filtros.situacao"
                      :items="situacoes"
                      label="Situação"
                      item-value="id"
                      item-text="descricao"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-model.trim="filtros.protocolo"
                      label="Protocolo"
                      type="text"/>
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-model="filtros.dataInicial"
                      label="Período Inicial"
                      type="date"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-model="filtros.dataFinal"
                      label="Período Final"
                      type="date"
                    />
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                :loading="loadingBtn"
                color="primary"
                @click="pesquisar">Pesquisar</v-btn>
            </v-card-actions>
          </v-card>
        </material-card>
      </v-flex>
      <v-flex md12>
        <v-card>
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Lotes RPS</v-toolbar-title>
            <v-divider
              class="mx-2"
              inset
              vertical />
            <v-spacer />
            <v-btn
              small
              fab
              color="primary">
              <v-icon @click="newItem()">mdi-plus</v-icon>
            </v-btn>
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headers"
            :items="items"
            :loading="true"
            :total-items="totalItems"
            :pagination.sync="pagination"
            class="elevation-1 arrow-pagintion"
          >
            <template
              slot="items"
              slot-scope="{ item }"
              ma-5>
              <td class="text-xs-center">{{ item.loteId }}</td>
              <td>{{ item.razaoSocial }}</td>
              <td v-if="!!item.dhEnvio">{{ item.dhEnvio | moment("DD/MM/YYYY H:mm") }}</td>
              <td v-else>{{ '-' }}</td>
              <td v-if="!!item.dhRecebimento">{{ item.dhRecebimento | moment("DD/MM/YYYY H:mm") }}</td>
              <td v-else>{{ '-' }}</td>
              <td>{{ item.protocolo }}</td>
              <td>
                <core-status-rps
                  :status="item.situacaoStr"
                  :mensagem="item.correcaoErro"
                />
              </td>
              <td
                v-if="item.linkNota"
                class="text-xs-center"
              >
                <a
                  :href="item.linkNota"
                  target="_blank">
                  <v-icon
                    color="primary"
                    title="Download Nota Fiscal"
                  >mdi-cloud-download-outline</v-icon>
                </a>
              </td>
              <td
                v-else
                class="text-xs-center"
              >-</td>
              <td class="justify-end layout ma-2">
                <v-icon
                  class="mr-2"
                  color="primary"
                  title="Visualizar RPS"
                  @click="visualizarLoteRps(item)"
                >mdi-magnify</v-icon>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { RpsBusiness, TiposBusiness } from '../../business'
import { TODAS_ID } from '../../constants'

export default {
  metaInfo: {
    titleTemplate: '%s - RPS'
  },
  data () {
    return {
      dialog: false,
      item: null,
      pagination: {
        rowsPerPage: 15
      },
      selected: [],
      situacoes: [],
      filtros: {
        redeId: TODAS_ID,
        empresaId: TODAS_ID,
        lojaId: TODAS_ID,
        situacao: TODAS_ID,
        protocolo: null,
        dataInicial: null,
        dataFinal: null
      },
      search: '',
      headers: [
        {
          sortable: false,
          text: 'Lote',
          value: 'loteId',
          align: 'center'
        },
        {
          sortable: false,
          text: 'Loja',
          value: 'razaoSocial'
        },
        {
          sortable: false,
          text: 'Enviado em',
          value: 'dhEnvio'
        },
        {
          sortable: false,
          text: 'Processado em',
          value: 'dhRecebimento'
        },
        {
          sortable: false,
          text: 'Protocolo',
          value: 'protocolo'
        },
        {
          sortable: false,
          text: 'Situação',
          value: 'situacaoStr'
        },
        {
          sortable: false,
          text: 'Nota Fiscal',
          value: 'notaFiscal',
          align: 'center'
        },
        {
          sortable: false,
          text: '',
          value: ''
        }
      ],
      items: [],
      totalItems: null,
      loading: false,
      loadingBtn: false
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  beforeMount () {
    TiposBusiness.getAllSituacaoRps()
      .then(response => { this.situacoes = response.data })
  },
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
    },
    newItem () {
      this.$router.push('/rps-cadastro')
    },
    visualizarLoteRps (item) {
      this.$router.push(`/rps-cadastro/${item.loteId}`)
    },
    pesquisar () {
      this.loadingBtn = true
      this.paginar()
    },
    paginar () {
      this.loading = true
      this.loadingBtn = true
      RpsBusiness.paginationLote(this.pagination.rowsPerPage, this.pagination.page - 1, this.pagination.sortBy || 'dhEnvio', this.filtros)
        .then(response => {
          this.items = response.data.content[0]['lotes']
          this.totalItems = response.data.content[0]['qtdItems']
        }).finally(() => {
          this.loading = false
          this.loadingBtn = false
        })
    }
  }
}
</script>
